import React, { Component } from 'react';
import fetch from 'isomorphic-unfetch';
import Perfiles from '../componentes/secciones/Perfiles';
import Navegacion from '../componentes/sources/Navegacion';
import '../sass/global.sass';
import '../sass/menu.sass';
import '../sass/txt_content.sass';


class  PeriodoUnoPerfiles extends Component {
    
    static async getInitialProps() {
        try {
            const res = await fetch('https://especiales.caracoltv.com/loscaminosdelvallenato/services/periodo-i/perfiles.json')
            const data = await res.json();
            return { data }
        } catch {
            console.log("Error al consumir el servicio")            
            const data = null
            return { data }
        }
    }

    componentWillMount() {
        this.setState({
            data: this.props.data
        })
    }

    render() { 
        let renderComponent = <Perfiles perfiles={this.state.data}/>
        if (this.state.data === null) {
            renderComponent="Servicio no disponible - Recarga la p√°gina para volver a intentarlo"
        }
        return (
            
            <React.Fragment>
                <div id="perfiles-p1" className="content txt-content">
                    <Navegacion 
                        menu="oculto"
                        navperidodos="visible"
                        link="periodos"
                        seccion="perfiles"
                    />
                    <h1 className="title-seccion-generico">Perfiles</h1>
                    <div className="header-seccion">
                        <span>Finales del siglo XIX - 1940</span>
                    </div>
                    <section id="seccion-periodo1perfiles" className="seccion-content">
                        {renderComponent}
                    </section>
                </div>
            </React.Fragment>
        );
    }
}
 
export default PeriodoUnoPerfiles;